/* Navigation bar */
.navbar {
  font-size: 1rem;
  font-weight: bold;
  position: fixed;
  width: 100%;
  height: 5rem;
  z-index: 3;
  padding: 0.5rem 4rem;
  transition: 0.3s background-color;
  -webkit-backdrop-filter: blur(0.5rem);
          backdrop-filter: blur(0.5rem);
}

.navbar-logo,
.navbar-logo-full {
  height: 4rem;
  cursor: pointer;
}

/* Only for larger screens */
@media screen and (min-width: 992px) {
  /* Branding */
  .navbar-logo {
    position: relative;
    transition: 0.3s;
  }
  .navbar-logo-full {
    position: absolute;
    transition: 0.3s;
  }

  /* Logo animations */
  .navbar-light .navbar-logo-full {
    left: 4rem;
    opacity: 1;
  }

  .navbar-dark .navbar-logo-full {
    left: 3rem;
    opacity: 0;
  }

  .navbar-dark .navbar-logo {
    opacity: 1;
    left: 0;
  }

  .navbar-light .navbar-logo {
    left: 1.1rem;
    opacity: 0;
  }
}

/* Navbar Background color */
.navbar.navbar-light.bg-light {
  background-color: transparent !important;
}

.navbar.navbar-dark.bg-dark {
  background-color: rgba(22, 25, 27, 0.95) !important;
  box-shadow: 0rem 0.5rem 2rem rgba(0, 0, 0, 0.7);
}

/* Navlist Items */
.navbar .navbar-nav .nav-item .nav-link {
  padding: 0rem 3rem;
  color: white;
  cursor: pointer;
}

.navbar .navbar-nav .nav-item .nav-link.current,
.navbar .navbar-nav .nav-item .nav-link:hover {
  color: var(--green);
}

/* Navlist toggle */
.navbar .navbar-toggler .navbar-toggler-icon {
  background-image: url(/static/media/logo.cd8d62a9.png);
}

.navbar .navbar-toggler .navbar-toggler-icon::after {
  content: url(/static/media/up-arrow.6cef2db9.svg);
}

/* Mobile */
@media screen and (max-width: 991px) {
  .navbar.navbar-dark.bg-dark {
    background-color: black !important;
  }

  /* Hide logo */
  .navbar .navbar-logo,
  .navbar .navbar-logo-full {
    display: none;
  }
  /* Div with links */
  .navbar .navbar-collapse {
    position: fixed;
    top: 0;
    padding: 6rem 1rem;
    background-color: rgba(0, 0, 0, 0.95);
    height: 100vh;
    min-width: 33vw;
    right: -100%;
    text-align: right;
    transition: 0.1s right;
  }
  .navbar .navbar-collapse.show {
    right: 0;
    z-index: -1;
  }
  .navbar .navbar-nav .nav-item {
    padding: 1rem 0rem;
  }
  .navbar .navbar-nav > *:not(:last-child) {
    border-bottom: 1px solid white;
  }

  /* Navlis toggle */
  .navbar .navbar-toggler {
    border: none;
    box-shadow: none;
  }
  .navbar .navbar-toggler .navbar-toggler-icon {
    background-size: cover;
    height: 2rem;
  }
  .navbar .navbar-toggler .navbar-toggler-icon::after {
    display: block;
    position: relative;
    right: 3rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    -webkit-filter: invert(56%) sepia(54%) saturate(561%) hue-rotate(104deg)
      brightness(89%) contrast(94%);
            filter: invert(56%) sepia(54%) saturate(561%) hue-rotate(104deg)
      brightness(89%) contrast(94%);
    transition: 0.3s;
  }
  .navbar .navbar-toggler.collapsed .navbar-toggler-icon::after {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    -webkit-filter: invert(100%);
            filter: invert(100%);
  }
}

.countdown {
  height: 40rem;
  min-height: 100vh;
  background-image: url(/static/media/bg.31d43b6d.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgb(0, 0, 0)),
    url(/static/media/bg.31d43b6d.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: white;
  font-size: 1rem;
}

.countdown > .container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.countdown-timer {
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
  text-align: center;
}

.countdown-timer > * {
  padding: 1rem;
  font-size: 1rem;
}

.countdown-number {
  display: flex;
  flex-direction: column;
  font-weight: bold;
}

.countdown-number-num {
  font-size: 5rem;
  letter-spacing: 1rem;
}

.countdown-number-label {
  font-size: 1rem;
}

.countdown-chevron {
  position: relative;
  font-size: 5rem;
  height: 0;
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
  cursor: pointer;
  transition: 0.3s color;
}
.countdown-chevron:hover {
  color: var(--green);
}

.countdown-title {
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 0.2rem;
}
.countdown-battle {
  color: var(--green);
}

@-webkit-keyframes pulse {
  0% {
    opacity: 1;
    top: 0.5rem;
  }
  50% {
    opacity: 0.2;
    top: 0rem;
  }
  100% {
    opacity: 1;
    top: 0.5rem;
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
    top: 0.5rem;
  }
  50% {
    opacity: 0.2;
    top: 0rem;
  }
  100% {
    opacity: 1;
    top: 0.5rem;
  }
}

@media screen and (min-width: 992px) {
  .countdown-number {
    width: 13rem;
  }
}
@media screen and (max-width: 991px) {
  .countdown {
    text-align: center;
  }
  .countdown-timer {
    flex-direction: column;
  }
  .countdown-timer > div {
    padding: 0;
  }
  .countdown-timer > div:nth-child(even) {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
}

/* Hero-section main div */
.hero-section {
  height: 40rem;
  min-height: 100vh;
  padding: 5rem;
  background-image: url(/static/media/hero-bg.78656a43.jpg);
  background-image: linear-gradient(90deg, transparent 30%, black 80%),
    url(/static/media/hero-bg.78656a43.jpg);
  /* background: linear-gradient(90deg, transparent, black 80%); */
  background-position: center;
  background-size: cover;
  color: white;
  overflow: hidden;
}

.hero-section .container,
.hero-section .row {
  height: 100%;
}

/* Text within hero div */
.hero-section .hero-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  text-align: left;
  font-weight: bold;
}

/* Hero content children*/
.hero-section .hero-content > * {
  padding: 1rem 0rem;
}

/* Hero Header */
.hero-section .hero-header {
  font-size: 3rem;
  font-weight: bold;
}

/* Highlighting key words in heading*/
.hero-section .hero-header .key-word {
  color: var(--green);
}

.hero-section .hero-header-wrapper {
  min-height: 13rem;
}

/* Text */
.hero-section .hero-desc {
  font-size: 1.5rem;
  font-weight: normal;
}

/* Hero Button */
.hero-button {
  background: transparent;
  position: relative;
  width: 14rem;
  height: 3rem;
  color: var(--green);
  transition: 0.3s;
}
.hero-button::before,
.hero-button::after {
  content: "";
  display: block;
  position: absolute;
  width: 1rem;
  height: 1rem;
  transition: width 0.3s, height 0.3s 0.3s;
}

.hero-button::before {
  top: -0.25rem;
  left: -0.25rem;
  border-top: 1px solid var(--green);
  border-left: 1px solid var(--green);
}

.hero-button::after {
  bottom: -0.25rem;
  right: -0.25rem;
  border-right: 1px solid var(--green);
  border-bottom: 1px solid var(--green);
}

.hero-button:hover::before,
.hero-button:hover::after {
  width: calc(100% + 0.5rem);
  height: calc(100% + 0.5rem);
}
.hero-section .hero-button:hover {
  background: var(--green);
  color: black;
}

/* Smaller screen size */
@media screen and (max-width: 991px) {
  .hero-section {
    background-image: linear-gradient(180deg, transparent 30%, black 90%),
      url(/static/media/hero-bg.78656a43.jpg);
    padding: 5rem 1rem;
    background-position: 30%;
  }
  .hero-section .hero-content {
    align-items: center;
    justify-content: flex-end;
  }
  .hero-section .hero-header-wrapper {
    min-height: 0.25rem;
  }
}

