/* Colors */
:root {
  --green: #14db8c;
  --green-transparent: #14db8b91;

  /* Socials */
  --instagram: #f706ae;
  --facebook: #0350f5;
  --linkedin: #00b7ff;
}

/* Fonts */

* {
  vertical-align: baseline;
  font-weight: inherit;
  font-family: "Futura PT";
  font-style: inherit;
  font-size: 100%;
  border: 0 none;
  outline: 0;
  padding: 0;
  margin: 0;
}

/* Animate fade in */
.fade-in {
  -webkit-animation: fade-in 0.4s forwards;
          animation: fade-in 0.4s forwards;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0.25;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0.25;
  }
  100% {
    opacity: 1;
  }
}

.section-title {
  font-weight: bold;
}

@media screen and (max-width: 991px) {
  html {
    font-size: 12px;
  }
}

